<script>
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import RdPointTags from './RdPointTags';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import * as changeCase from "change-case";
import { getTypeNames, getTypeTags } from '../../utils/tagsUtils';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, TOPIC, POINT_ADVANCED_HEADERS, POINT_EDIT_ALL_DEFAULT, } from './rubixDriverConstant';
import { toUpperTextKey } from '../../utils/utils';

export default {
  components: {
    RdPointTags, 
    MqttConsole, 
    RightSidebar, 
    ConfirmDialog,
    SystemStatusTable
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    tableProps: {...DATA_TABLE_PROPS},
    topic: TOPIC,
    rules: {...RULES},
    pointHeaders: [...POINT_ADVANCED_HEADERS],
    editAll: {...POINT_EDIT_ALL_DEFAULT},
    dialog: false,
    dialogDelete: false,
    dialogMassEdit: false,
    tab: 0,
    editedIndex: -1,
    action: "",
    device: {},
    valid: false,
    selected: [],
    tags: [],
    currentItem: {},
    editedItem: {},
    dirs: [{text: '..', link: 'cloud-networks'}],
    search:"",
    editField: "",
    intervalId: null,
    pointsOfDevice: [],
    network: {},
    allPoints: [],
    dirs: [],
    typeNames: getTypeNames(),
    loadingData: false,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Create Point" : "Edit Point";
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.driverRubix;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData () {
      return this.$store.state.rdNetworks;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    tags(val) {
      // this.setTagStr(val);
      this.onChangeTag(val);
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
      next();
    });
  },
  mounted() {
    this.getAllPoints();

    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getAllPoints();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      this.loadingData = true;
      axios.get(`point-server/generic/network/${this.$route.params.id}?with_children=true&points=true`).then(data => {
        this.network = data;
        this.getDataTable(data.devices);
        //get dirs
        if (this.dirs.length != 2) {
          this.getDirs();
        }
      }).finally(() => {
        this.loadingData = false;
      });
    },
    getDataTable(devices){
      this.allPoints = [];
      devices.forEach(device=>{
        if(device.points.length > 0){
          const pointsPerDevice = device.points.map(point=>{
            const keys = Object.keys(point.point_store);
            if (keys && keys.length > 0) {
              keys.forEach(key => {
                point = { ...point, [key]: point.point_store[key], category: device.name };
              });
            }
            delete point.point_store;
            delete point.point_uuid;
            return point;
          });
          this.allPoints = this.allPoints.concat(pointsPerDevice);
        }
      });
    },
    onChangeTag(tags) {
      if(tags.length == 0) return;
      let strTags = '';
      if (Array.isArray(tags)) {
        const objTags = {};
        for (const tag of tags) {
          if (tag.key && tag.value) {
            objTags[tag.key] = tag.value;
          }
        }
        strTags = JSON.stringify({...objTags});
      } else {
        strTags = JSON.stringify(tags);
      }
      this.editAll[this.editField] = strTags;
      this.editedItem.tags = strTags;
    },
    editItem(item) {
      this.editedIndex = this.allPoints.findIndex(p=>p.uuid == item.uuid);
      this.pointsOfDevice = this.allPoints.filter(point=>point.device_uuid === item.device_uuid);
      this.editedItem = Object.assign({}, item);
      const arrTags = [];
      if (item.tags) {
        const tags = JSON.parse(item.tags);
        for (const tag in tags) {
          arrTags.push({key: tag, value: tags[tag]});
        }
      }
      this.tags = arrTags;
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.allPoints.indexOf(this.currentItem);
      this.allPoints.splice(this.editedIndex, 1);
      await axios.delete(`point-server/generic/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    isExistingvalue(item, fieldName) {
      let value = item[fieldName];
      let points = _cloneDeep(this.pointsOfDevice);
      const index = points.findIndex(p=>p.uuid == item.uuid);
      points.splice(index, 1);
      return points.find(p => p[fieldName] == value) != null;
    },
    updatePoint(data) {
      if(this.$refs.comboBox) this.$refs.comboBox.blur();
      this.$nextTick(() => {
        data.name = this.setting.enableChangeCase ? changeCase[this.setting.caseType](data.name) : data.name;
        data.fallback_value = !isNaN(parseFloat(data.fallback_value)) ? parseFloat(data.fallback_value) : null;
        axios.put(`point-server/generic/point/${data.uuid}`, data )
          .then((response) => {
            const index = this.allPoints.findIndex(n => n.uuid == response.uuid);
            Object.assign(this.allPoints[index], response);
            this.close();
            this.$toasted.show('update success');
          })
          .catch((error) => { this.$toasted.show('update fail', {type: 'error'}); });
      });
      
    },
    backgroundColor(item) {
      let classColor = '';
      if(item.fault){
        classColor = 'background-fault-true';
      }else{
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = {...POINT_EDIT_ALL_DEFAULT};
      this.tags = [];
    },
    numberChange(){
      if(!this.editAll[this.editField] && this.editField !== 'fallback_value')
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.allPoints.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    getDirs(){
      this.dirs = [{ text: "NETWORKS", link: '/rubix-driver/networks'}];
      this.dirs.push({ text: `NET: ${this.network.name}`, disabled: true });
    },
    onClickRefresh(){
      this.getAllPoints();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if(!e) this.selected = [...this.selected].filter(point=>point.device_uuid != items[0].device_uuid);
      else this.selected = [...this.selected].concat(items);
    },
    onChangeTypeName(val) {
      if(!this.typeNames.includes(val)) {
        this.tags = [];
      } else {
        const arrTags = this.tags = [];
        const typeTags = getTypeTags(val);   
        for (const tag of typeTags) {
          arrTags.push({key: tag, value: tag});
        }
        this.tags = arrTags;
      }
    },
  },
};
</script>

